@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: 'Montserrat', sans-serif; }

a {
  text-decoration: none; }

body {
  height: auto;
  background-color: #0a192f; }

.content {
  width: 80%;
  height: auto;
  margin: auto;
  color: #8892b0;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.section {
  position: relative;
  min-height: 90vh;
  height: auto;
  padding-top: 10vh;
  margin: 0 10vh; }
  .section hr {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%; }

footer {
  background-color: #020c1b;
  height: 30px;
  width: 100%;
  color: #8892b0; }

.title {
  margin-top: 25px;
  color: turquoise; }

.flashy-link {
  display: inline-block;
  border: thin solid turquoise;
  background-color: transparent;
  color: turquoise;
  padding: .75em 1.5em;
  border-radius: 5px;
  margin-left: 3%;
  -webkit-transition: .25s;
  transition: .25s; }

.flashy-link:hover {
  background: rgba(64, 224, 208, 0.1); }

.nav-bar {
  background-color: #020c1b;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: 10vh;
  padding: 15px 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  z-index: 100;
  box-shadow: 0 8px 6px -6px rgba(0, 0, 0, 0.4); }

.nav-links {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

#home {
  width: 50px;
  height: 50px;
  background-image: url(/static/media/logo.b7d3bfed.jpg);
  background-size: cover;
  margin-right: auto; }

ol {
  width: 40%;
  display: inline-block;
  font-size: 1.25em; }
  ol li {
    color: turquoise; }
    ol li a::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      bottom: 0;
      left: 0;
      background-color: turquoise;
      visibility: hidden;
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
      -webkit-transition: all 0.3s ease-in-out 0s;
      transition: all 0.3s ease-in-out 0s; }
    ol li a {
      position: relative;
      margin-left: 10px;
      color: #8892b0; }
    ol li a:hover {
      color: turquoise; }
    ol li a:hover::before {
      visibility: visible;
      -webkit-transform: scaleX(1);
              transform: scaleX(1); }

#intro-greeting, #intro-name, #intro-statement, #intro-description, #intro-contact-me {
  margin: 25px 0; }

#intro-greeting {
  color: turquoise;
  font-size: 1em; }

#intro-name {
  color: lavender;
  font-size: 5em; }

#intro-statement {
  color: #8892b0;
  font-size: 4em; }

#intro-description {
  color: #8892b0;
  font-size: 1em;
  line-height: 1.5em;
  letter-spacing: 2px;
  margin-bottom: 3em; }

#intro-contact-me {
  font-size: 1.5em; }

#about-body {
  display: flex;
  justify-content: space-between; }
  #about-body h3 {
    width: 70%;
    padding: 3em 3em 3em 0;
    line-height: 1.5em;
    letter-spacing: 2px; }
  #about-body div {
    width: 30%;
    display: flex;
    align-items: center; }
    #about-body div img {
      width: 100%; }

h2, #experience-list {
  margin: 25px 0; }

#experience-list {
  display: flex;
  justify-content: space-between;
  margin-right: 25%; }
  #experience-list ul h1 {
    color: lavender; }
  #experience-list ul li {
    font-size: 1.5em;
    margin: 10px 10px 10px 25px; }

#work-cards {
  display: flex;
  flex-wrap: wrap; }

.work-card {
  position: relative;
  padding: 3%;
  margin: 3%;
  width: 40%;
  box-shadow: rgba(2, 12, 27, 0.7) 0px 10px 30px -15px;
  background-color: #172a45;
  -webkit-transition-duration: .25s;
          transition-duration: .25s; }
  .work-card .work-position {
    color: lavender;
    margin: 10px 0; }
  .work-card .work-company {
    color: turquoise;
    margin: 10px 0; }
  .work-card .work-duration {
    margin: 10px 0; }
  .work-card ul {
    margin-top: 20px; }
    .work-card ul li {
      margin: 20px 0; }

.work-card:hover {
  -webkit-transform: translateY(-5%);
          transform: translateY(-5%); }

.word-card:hover.clicked {
  color: black; }

.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  height: auto; }

.project-section {
  margin-top: 25px; }

.project {
  height: auto;
  width: 30%;
  padding: 3%;
  margin: 3%;
  -webkit-transition: .25s;
  transition: .25s;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: space-around;
  align-items: center;
  background-color: #172a45;
  box-shadow: rgba(2, 12, 27, 0.7) 0px 10px 30px -15px; }
  .project * {
    margin: 10px 0; }
  .project .project-title {
    color: lavender; }
  .project .project-page {
    width: 100%; }
  .project .page {
    margin: auto;
    height: 75vh;
    width: 120vh; }
  .project .project-information {
    width: 100%;
    display: flex;
    justify-content: space-around;
    text-align: left; }
    .project .project-information h1 {
      text-align: center; }

.project.active {
  width: 100%;
  padding: 3%;
  margin: 3%; }

#contact-body {
  display: flex;
  flex-direction: row;
  justify-content: space-around; }

#contact-immediate {
  width: 40%; }

#contact-information {
  width: 40%;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  line-height: 2em;
  font-size: 1.25em; }
  #contact-information a {
    color: turquoise; }

form {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.input-field {
  position: relative;
  margin-top: 35px;
  padding: 20px 0;
  color: #8892b0;
  width: 100%; }
  .input-field label {
    position: absolute;
    left: 10px;
    top: 10px;
    -webkit-transition: .25s;
    transition: .25s;
    font-weight: normal; }
  .input-field input, .input-field textarea {
    position: absolute;
    top: 0;
    left: 0;
    display: inline;
    outline: none;
    border: none;
    border-bottom: thin solid black;
    padding-left: 10px; }
  .input-field input {
    height: 100%;
    width: 100%; }
  .input-field input:valid ~ label,
  .input-field input:focus ~ label,
  .input-field textarea:valid ~ label,
  .input-field textarea:focus ~ label {
    top: -25px;
    color: turquoise; }

#message {
  height: 30vh;
  width: 100%; }
  #message textarea {
    resize: none;
    height: 100%;
    width: 100%; }

#contact-send {
  margin: 25px auto auto;
  width: 100px; }

